export default ".btn {\n  background: var(--dark);\n  color: var(--light);\n  padding: 0.6rem 1.3rem;\n  text-decoration: none;\n  border: 0;\n}\n\nimg {\n  max-width: 100%;\n}\n\n.wrapper {\n  display: grid;\n  grid-gap: 20px;\n}\n\n/* Navigation */\n\n.main-nav ul {\n  display: grid;\n  grid-gap: 20px;\n  padding: 0;\n  list-style: none;\n  grid-template-columns: repeat(4, 1fr);\n}\n\n.main-nav a {\n  background: var(--primary);\n  display: block;\n  text-decoration: none;\n  padding: 0.8rem;\n  text-align: center;\n  color: var(--dark);\n  text-transform: uppercase;\n  font-size: 1.1rem;\n  box-shadow: var(--shadow);\n}\n\n.main-nav a:hover {\n  background: var(--dark);\n  color: var(--light);\n}\n\n/* Top Container */\n\n.top-container {\n  display: grid;\n  grid-gap: 20px;\n  grid-template-areas: \"showcase showcase top-box-a\" \"showcase showcase top-box-b\";\n}\n\n/* Showcase */\n\n.showcase {\n  grid-area: showcase;\n  min-height: 400px;\n  background: url(https://images.unsplash.com/photo-1554248889-15c8ca7dbba4?ixlib=rb-1.2.1&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1080&fit=max&ixid=eyJhcHBfaWQiOjEyMDd9);\n  background-size: cover;\n  background-position: center;\n  padding: 3rem;\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  justify-content: center;\n  box-shadow: var(--shadow);\n}\n\n.showcase h1 {\n  font-size: 4rem;\n  margin-bottom: 0;\n  color: var(--light);\n}\n\n.showcase p {\n  font-size: 1.3rem;\n  margin-top: 0;\n  color: var(--light);\n}\n\n/* Top Box */\n\n.top-box {\n  background: var(--primary);\n  display: grid;\n  align-items: center;\n  justify-items: center;\n  box-shadow: var(--shadow);\n  padding: 1.5rem;\n}\n\n.top-box .price {\n  font-size: 2.5rem;\n}\n\n.top-box-a {\n  grid-area: top-box-a;\n}\n\n.top-box-b {\n  grid-area: top-box-b;\n}\n\n/* Boxes */\n\n.boxes {\n  display: grid;\n  grid-gap: 20px;\n  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n}\n\n.box {\n  background: var(--primary);\n  text-align: center;\n  padding: 1.5rem 2rem;\n  box-shadow: var(--shadow);\n}\n\n/* Info */\n\n.info {\n  background: var(--primary);\n  box-shadow: var(--shadow);\n  display: grid;\n  grid-gap: 30px;\n  grid-template-columns: repeat(2, 1fr);\n  padding: 3rem;\n}\n\n/* Portfolio */\n\n.portfolio {\n  display: grid;\n  grid-gap: 20px;\n  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n}\n\n.portfolio img {\n  width: 100%;\n  box-shadow: var(--shadow);\n}\n\n/* Footer */\n\nfooter {\n  margin-top: 2rem;\n  background: var(--dark);\n  color: var(--light);\n  text-align: center;\n  padding: 1rem;\n}\n\n/* Media Queries */\n\n@media (max-width: 700px) {\n  .top-container {\n    grid-template-areas: \"showcase showcase\" \"top-box-a top-box-b\";\n  }\n\n  .showcase h1 {\n    font-size: 2.5rem;\n  }\n\n  .main-nav ul {\n    grid-template-columns: 1fr;\n  }\n\n  .info {\n    grid-template-columns: 1fr;\n  }\n\n  .info .btn {\n    display: block;\n    text-align: center;\n    margin: auto;\n  }\n}\n\n@media (max-width: 500px) {\n  .top-container {\n    grid-template-areas: \"showcase\" \"top-box-a\" \"top-box-b\";\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"